import React, {useContext, useState } from 'react'

//context
import { CartContext }  from '../../context/CartContext'

//bootstrap
import {LinkContainer} from 'react-router-bootstrap'
import { Jumbotron, Button, Table } from 'react-bootstrap'
import { PlusCircle, DashCircle, XCircle } from 'react-bootstrap-icons';

//import de componentes
import Orders from './Orders'
import Exit from './Exit'




export default function Cart() {

    const [idProd, setIdProd, hacer, total, setTotal, unit, setUnit, showToast, setShowToast] = useContext(CartContext)
    
    
   return (
      <> 
        
        { unit === 0 ?  
        
          <Jumbotron  className="container mt-5">
            <h1>Carrito vacio</h1>
              
            <p>
            <LinkContainer to="/"><Button variant="primary">Regresar</Button></LinkContainer>
            </p>
          </Jumbotron>
          : 
          
          <>
            <Table striped bordered hover sm className="mt-5">
              <thead>
                <tr>
                  <th>Producto</th>
                  <th style={{ minWidth: '7em'}}>Cantidad</th>
                  <th>Nombre</th>
                  <th>Precio</th>
                  <th>Eliminar</th>
                </tr>
              </thead>
              <tbody>
                {idProd.map(cart =>
                              <tr key={cart.id} 
                                  style={{ backgroundColor:'white' }}>
                                <td>
                                  <img src={cart.img} alt="productos" style={{ maxWidth:'50px', maxHeight:'50px' }}/>
                                </td> 
                                <td>
                                  <DashCircle size={22}  onClick={()=> hacer.borrar(cart.id)}/>
                                  <span className="m-1">{cart.cantidad}</span>       
                                  <PlusCircle  size={22} onClick={()=> hacer.agregar(cart.id)}/>
                                </td> 
                                <td>{cart.name}</td>
                                <td>{cart.price}</td>  
                                <td>
                                  <XCircle onClick={()=> hacer.eliminar(cart.id)} size={22} />
                                                                
                                </td>  
                              </tr>
                )
                }     
              </tbody>
              
            </Table>
            <div className="tfoot">  
                <h5>Unidades: {hacer.unit}</h5>
                <h5>Total: {hacer.total} </h5>
                <button className="btn btn-danger" onClick={hacer.vaciar}> Vaciar Carrito </button>
                
                { hacer.user == null ? <LinkContainer to="User">
                  <button className="btn btn-success" > LogIn / Registrarse </button>
                </LinkContainer> : 
                <button className="btn btn-success" onClick={hacer.handleCompra}> Finalizar Pedido </button>}
                <LinkContainer to="/">
                  <button className="btn btn-primary" > Regresar </button>
                </LinkContainer>                         
            </div>
              <Exit showToast={showToast} setShowToast={setShowToast} texto={"Realizo su compra correctamente"} />
              <Orders />
                
          </> 
        }
      </> 
    ) 
}
